@font-face {
  font-family: "lato-100";
  src: url("../fonts/lato-100.woff") format("woff"), url("../fonts/lato-100.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "lato-300";
  src: url("../fonts/lato-300.woff") format("woff"), url("../fonts/lato-300.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "lato-400";
  src: url("../fonts/lato-400.woff") format("woff"), url("../fonts/lato-400.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "lato-500";
  src: url("../fonts/lato-500.woff") format("woff"), url("../fonts/lato-500.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "lato-700";
  src: url("../fonts/lato-700.woff") format("woff"), url("../fonts/lato-700.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "lato-black";
  src: url("../fonts/lato-black.woff") format("woff"), url("../fonts/lato-black.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

//Fonts
$fontThink: "lato-100";
$fontLight: "lato-300";
$fontRegular: "lato-400";
$fontMedium: "lato-500";
$fontBold: "lato-700";
$fontBlack: "lato-black";
//Media
$xs: 599px;
$sm: 767px;
$sm: 1023px;
$md: 1130px;
$xl: 1200px;
@mixin respond($media) {
  @if $media==phone {
    @media only screen and (max-width: $xs) {
      @content;
    }
  } @else if $media==smallTablet {
    @media only screen and (max-width: $sm) {
      @content;
    }
  } @else if $media==tablet {
    @media only screen and (max-width: $sm) {
      @content;
    }
  } @else if $media==smalldesk {
    @media only screen and (max-width: $md) {
      @content;
    }
  } @else if $media==desktop {
    @media only screen and (max-width: $xl) {
      @content;
    }
  }
}

// Colors
$primaryColor: #910f27;
$secondColor: #8cbd26;
$textColor: #4c575d;
/* Global Style Start */

body,
html {
  font-size: 14px;
  font-family: $fontRegular;
  height: 100%;
  line-height: 1.2;
}

a {
  color: currentColor;
  text-decoration: none;
  &:hover {
    color: $primaryColor;
  }
}
.ce-icon {
  width: 20px;
  height: 20px;
  fill: currentColor;
}
.ce-container {
  max-width: 1310px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: {
    left: 15px;
    right: 15px;
  }
  @include respond(smalldesk) {
    max-width: 100%;
    padding: {
      left: 10px;
      right: 10px;
    }
  }
}
/* Select Start */
.ce-select {
  & + .ui-selectmenu-button.ui-button {
    background-color: #ffffff;
    border: 1px solid #000000;
    border-radius: 0;
    height: 42px;
    box-sizing: border-box;
    font-size: 16px;
    width: 100%;
    max-width: 100%;
    // @include respond(phone) {
    //   max-width: 200px;
    // }
    & .ui-selectmenu-text {
      height: 100%;
      line-height: 2;
    }
    & .ui-selectmenu-icon {
      background-image: url("../images/icons-png/select-arrows.png");
      background-position: center;
      width: 20px;
      height: 30px;
      background-repeat: no-repeat;
    }
  }
}
.ui-selectmenu-menu .ui-front .ui-selectmenu-open {
  & .ui-menu-item .ui-state-active {
    background-color: $primaryColor;
    color: #ffffff;
  }
}
/* Select End */

/* Input Start */
.ce-input {
  position: relative;
  background-color: #ffffff;
  border: 1px solid #000000;
  border-radius: 0;
  height: 42px;
  box-sizing: border-box;
  font-size: 16px;
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #646464;
  font-size: 16px;
  padding-left: 15px;
  padding-right: 35px;
  cursor: pointer;
  & input {
    border: none;
    height: 100%;
    box-sizing: border-box;
    width: 100%;
    color: #646464;
    font-size: 16px;
    &:focus {
      outline: none;
    }
  }
  &::after {
    content: url("../images/icons-png/select-arrows.png");
    position: absolute;
    top: 10px;
    right: 16px;
  }
}
/* Input End */

/* Button Start */
.ce-button {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  background-color: #1671d5;
  height: 41px;
  box-sizing: border-box;
  border: 1px solid #1671d5;
  font-size: 16px;
  color: #fafafa;
  text-transform: uppercase;
  padding-left: 20px;
  padding-right: 20px;
  transition: 0.5s;
  cursor: pointer;
  &:hover {
    background-color: #ffffff;
    color: #1671d5;
  }
  &--primary {
    background-color: $primaryColor;
    border-color: $primaryColor;
    &:hover {
      background-color: #ffffff;
      color: $primaryColor;
    }
  }
  &--light {
    background-color: rgba(#ffffff, 0);
    border-color: #fff;
    font-size: 16px;
    text-transform: none;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 140px;
    &:hover {
      background-color: #ffffff;
      color: $primaryColor;
    }
  }
  &--small {
    display: inline-flex;
  }
}
/* Button End */

.ce-slider-range {
  &.ui-slider {
  }
  &.ui-corner-all {
    border-radius: 0;
    border: 1px solid rgba(#1671d5, 0.3);
  }
  &.ui-slider-horizontal {
    height: 5px;
  }
  &.ui-widget {
  }
  &.ui-widget-content {
  }
  & .ui-widget-header {
    background-color: #1671d5;
  }
  & .ui-slider-handle {
    width: 20px;
    height: 20px;
    box-sizing: border-box;
    border-radius: 50%;
    background-color: #ffffff;
    border: 1px solid #1671d5;
    transform: translateY(-2px);
    transition: background-color 0.5s;
    cursor: pointer;
    &:focus {
      outline: none;
    }
    &:hover {
      background-color: #1671d5;
    }
  }
}

/*Checkbox Start*/
.ce-checkbox {
  display: flex;
  align-items: center;
  user-select: none;
  font-size: 19px;
  @include respond(phone) {
    font-size: 16px;
  }
  & input[type="checkbox"] {
    display: none;
    &:checked + .ce-checkbox__view {
      background-color: #910f27;
      background-image: url("../images/icons-png/checkbox-icon.png");
      background-position: center;
      background-repeat: no-repeat;
    }
    &[checked="checked"] + .ce-checkbox__view {
      background-color: #910f27;
      background-image: url("../images/icons-png/checkbox-icon.png");
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  &__view {
    width: 16px;
    height: 16px;
    background-color: #ffffff;
    border: 1px solid #4d4c4c;
    box-sizing: border-box;
    margin-right: 10px;
    transition: background-color 0.4s;
    flex-shrink: 0;
  }
  &__text {
  }
  &--part {
    & input[type="checkbox"] {
      &:checked + .ce-checkbox__view {
        background-color: #ffffff;
        position: relative;
        background-image: none;
        display: flex;
        justify-content: center;
        align-items: center;
        &::after {
          content: "";
          display: inline-block;
          width: 8px;
          height: 8px;
          background-color: #910f27;
          position: absolute;
          top: calc(50% - 4px);
          left: calc(50% - 4px);
        }
      }
      &[checked="checked"] + .ce-checkbox__view {
        background-color: #ffffff;
        position: relative;
        background-image: none;
        display: flex;
        justify-content: center;
        align-items: center;
        &::after {
          content: "";
          display: inline-block;
          width: 8px;
          height: 8px;
          background-color: #910f27;
          position: absolute;
          top: calc(50% - 4px);
          left: calc(50% - 4px);
        }
      }
    }
  }
  &--black {
    & .ce-checkbox__view {
      width: 24px;
      height: 24px;
      margin-top: 7px;
    }
    & input[type="checkbox"] {
      &:checked + .ce-checkbox__view {
        background-color: #ffffff;
        background-image: url("../images/icons-png/arrow-down-black.png");
        background-position: center;
        background-repeat: no-repeat;
      }
      &[checked="checked"] + .ce-checkbox__view {
        background-color: #ffffff;
        background-image: url("../images/icons-png/arrow-down-black.png");
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }
}

/*Checkbox End*/

/* Global Style End */

/* Header Start */
.ce-header {
  &__top {
    height: 50px;
    background-color: #ebe9e9;
    @include respond(phone) {
      display: none;
    }
    &-wrapper {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      height: 100%;
    }
    &-item {
      font-family: $fontMedium;
      color: #333333;
      font-size: 14px;
      margin-right: 18px;
    }
  }
  &__bottom {
    position: relative;
    background-color: #f6f6f6;
    height: 64px;
    &-wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 100%;
    }
    &-logo {
      margin-right: 85px;
      @include respond(smalldesk) {
        margin-right: 35px;
      }
      @include respond(phone) {
        max-width: 112px;
        margin-right: 0;
      }
      & img {
        max-width: 100%;
        width: 170px;
      }
    }
    &-phones {
      padding-left: 0;
      margin: 0;
      list-style: none;
      font-family: $fontBold;
      color: #353535;
      display: flex;
      align-items: center;
      margin-right: 85px;
      @include respond(smalldesk) {
        margin-right: 20px;
      }
      @include respond(tablet) {
        display: none;
      }
      & li {
        margin-right: 18px;
        &:last-of-type {
          margin-right: 0;
          @include respond(smalldesk) {
            display: none;
          }
        }
      }
      &:before {
        content: url("../images/icons-png/phone-icon.png");
        margin-right: 14px;
      }
    }
    &-allcontacts {
      &-title {
        color: #1671d5;
        font-size: 18px;
        cursor: pointer;
        @include respond(smallTablet) {
          font-size: 0;
          width: 20px;
          height: 20px;
          background-image: url("../images/icons-png/phone-icon-mobile.png");
          background-position: center;
          background-repeat: no-repeat;
        }
        &::after {
          content: url("../images/icons-png/arrow-down.png");
          margin-left: 18px;
          position: relative;
          bottom: 2px;
          @include respond(smallTablet) {
            content: "";
          }
        }
      }
    }
    &-search {
      position: relative;
      display: flex;
      align-items: center;
      background-color: #fff;
      border: 1px solid #353535;
      height: 32px;
      box-sizing: border-box;
      @include respond(phone) {
        position: fixed;
        right: 0;
        top: 64px;
        z-index: 5;
        transition: 0.8s;
        transform: translateX(100%);
      }
      &--isShow {
        transform: translateX(0);
      }
      & input[type="text"] {
        border: none;
        padding-left: 15px;
        color: #353535;
        font-size: 16px;
        font-family: $fontLight;
        &:focus {
          outline: none;
        }
      }
      & button {
        border: none;
        background-color: #fff;
        background-image: url("../images/icons-png/search-icon.png");
        background-position: center;
        background-repeat: no-repeat;
        height: 30px;
        width: 44px;
        box-sizing: border-box;
        border-left: 1px solid #353535;
      }
    }
    &-menu {
      width: 20px;
      flex-shrink: 0;
      flex-direction: column;
      display: none;
      @include respond(phone) {
        display: flex;
      }
      cursor: pointer;
      &-item {
        width: 100%;
        height: 2px;
        background-color: $primaryColor;
        margin-bottom: 4px;
        transition: 0.5s;
      }
      &::after {
        content: "";
        display: inline-block;
        width: 100%;
        height: 2px;
        background-color: $primaryColor;
        transition: 0.5s;
      }
      &::before {
        content: "";
        display: inline-block;
        width: 100%;
        margin-bottom: 4px;
        height: 2px;
        background-color: $primaryColor;
        transition: 0.5s;
      }
    }
    &-search-icon {
      width: 19px;
      height: 23px;
      background-image: url("../images/icons-png/search-icon-mobile.png");
      background-position: center;
      background-repeat: no-repeat;
      flex-shrink: 0;
      cursor: pointer;
      display: none;
      @include respond(phone) {
        display: block;
      }
    }
    &-signin {
      width: 19px;
      height: 23px;
      background-image: url("../images/icons-png/sign-in-icon.png");
      background-position: center;
      background-repeat: no-repeat;
      cursor: pointer;
      @include respond(smallTablet) {
        width: 21px;
        background-image: url("../images/icons-png/user-icon-mobile.png");
      }
    }
    &-basket {
      width: 25px;
      height: 27px;
      background-image: url("../images/icons-png/shopping-cart-icon.png");
      background-position: center;
      background-repeat: no-repeat;
      cursor: pointer;
      @include respond(smallTablet) {
        background-image: url("../images/icons-png/shoping-cart-icon-mobile.png");
      }
    }
    &.ce-sticky {
      position: fixed;
      width: 100%;
      z-index: 21;
      top: 0;
      left: 0;
      animation: stickyAnim 1s forwards;
    }
  }
  &__allcontacts {
    position: absolute;
    top: 64px;
    background-color: #e8e8e8;
    border: 1px solid #4d4c4c;
    padding: 28px;
    width: 100%;
    max-width: 345px;
    box-sizing: border-box;
    display: none;
    z-index: 25;
    @include respond(phone) {
      left: 0;
      width: 100%;
      max-height: calc(100vh - 64px);
      box-sizing: border-box;
      overflow: auto;
      max-width: 100%;
      &::before {
        display: none;
      }
    }
    &::before {
      content: "";
      position: absolute;
      top: -11px;
      left: 80px;
      width: 20px;
      height: 20px;
      border-top: 1px solid #000;
      border-right: 1px solid #000;
      background-color: #e8e8e8;
      transform: rotate(-45deg);
      @include respond(smallTablet) {
        left: 5px;
      }
    }
    &-title {
      font-size: 12px;
      color: #353535;
      font-family: $fontBlack;
      margin-bottom: 16px;
    }
    &-item {
      margin-bottom: 21px;
      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 1px;
        background-color: #050607;
        opacity: 0.6;
        margin-top: 17px;
      }
    }
    &-phones {
      padding-left: 0;
      margin: 0;
      list-style: none;
      font-size: 12px;
      color: #353535;
      margin-bottom: 19px;
      & li {
        margin-bottom: 16px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
    &-mail {
      font-size: 12px;
      color: #1671d5;
      text-decoration: underline;
      &::before {
        content: url("../images/icons-png/mail-icon.png");
        margin-right: 3px;
        position: relative;
        top: 2px;
      }
    }
    &-callback {
      display: flex;
      align-items: center;
      font-size: 12px;
      color: #1671d5;
      text-decoration: underline;
      transition: 0.5s;
      cursor: pointer;
      &:hover {
        color: $primaryColor;
      }
      &::before {
        content: url("../images/icons-png/callback-icon.png");
        margin-right: 7px;
        position: relative;
        top: 2px;
      }
    }
    &-info {
      font-size: 12px;
      color: #1671d5;
      text-decoration: underline;
    }
  }
}
/* Header End */

@keyframes stickyAnim {
  from {
    transform: translateY(-200px);
  }
  to {
    transform: translateY(0);
  }
}

/*Accardion Start*/
.ce-accordion {
  // & [data-accordion], [data-content]{
  //   overflow: initial!important;
  // }
  &.open {
    > .ce-filter {
      overflow: initial !important;
    }
  }
  &__header {
    height: 50px;
    box-sizing: border-box;
    background-color: #910f27;
    color: #ffffff;
    font-size: 20px;
    text-transform: uppercase;
    text-align: center;
    font-family: $fontMedium;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: none;
    padding-right: 10px !important;
    display: none;
    cursor: pointer;
    @include respond(phone) {
      display: flex;
    }
    &::after {
      top: auto !important;
      transition: 0.5s;
      background: url("../images/icons-png/arrow-down-icon.png") no-repeat center !important;
    }
    &--light {
      color: #353535;
      background-color: #ffffff;
      &::after {
        background: url("../images/icons-png/arrow-down-dark-icon.png") no-repeat center !important;
      }
    }
  }
  &__content {
    & ul {
      list-style: none;
      padding: 0;
      // padding-top: 20px;
      // padding-bottom: 20px;
      margin: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    & li {
      width: 100%;
      height: 40px;
      text-transform: uppercase;
      text-align: center;
      border-bottom: 1px solid $primaryColor;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  & > div[data-content] > * {
    border-bottom: none !important;
  }
}
/*Accardion End*/

/* Navigation Start */
.ce-nav {
  height: 61px;
  background-color: $primaryColor;
  @include respond(phone) {
    position: fixed;
    left: 0;
    top: 61px;
    z-index: 50;
    width: 100%;
    height: auto;
    padding-top: 20px;
    transform: translateY(-600px);
    transition: 1s;
  }
  &__wrapper {
    height: 100%;
  }
  &__items {
    padding-left: 0;
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    color: #ffffff;
    text-transform: uppercase;
    font-size: 14px;
    @include respond(phone) {
      flex-direction: column;
    }
    & > li {
      position: relative;
      @include respond(phone) {
        margin-bottom: 20px;
        &:first-of-type {
          display: none;
        }
      }
      &:hover > .ce-nav__submenu {
        transform: scaleY(1);
        opacity: 1;
      }
      & > a:hover {
        color: #ffffff;
        text-decoration: underline;
      }
    }
  }
  &__submenu {
    position: absolute;
    top: 37px;
    z-index: 5;
    background-color: #ffffff;
    border: 1px solid #4d4c4c;
    color: #353535;
    text-transform: uppercase;
    font-size: 10px;
    padding-left: 0;
    list-style: none;
    padding-left: 14px;
    padding-top: 24px;
    padding-right: 40px;
    padding-bottom: 45px;
    transform: scaleY(0);
    transform-origin: top;
    transition: 0.5s;
    transition-delay: 0.2s;
    opacity: 0;
    & li {
      margin-bottom: 18px;
      & a {
        white-space: nowrap;
      }
      &.ce-active {
        color: #1671d5;
      }
    }
  }
}
/* Navigation End */

/*Open mobile menu Start*/
.ce-mobile-menu-is-show {
  overflow: hidden;
  & .ce-nav {
    transform: translateY(0);
  }
  & .ce-header__bottom-menu-item {
    transform: translateX(-100%);
    opacity: 0;
  }
  & .ce-header__bottom-menu {
    &::after {
      transform: translate(2px, 2px) rotate(-45deg);
      transform-origin: 0;
    }
    &::before {
      transform: translate(2px) rotate(45deg);
      transform-origin: 0;
    }
  }
}
/*Open mobile menu End*/

/*Open mobile serch Start*/
.ce-mobile-search-is-show {
}
/*Open mobile serch End*/

/* Filter Start */
.ce-filter {
  background-color: #f1f1f1;
  // overflow: initial !important;
  height: 100%;
  opacity: 0;
  &__wrapper {
    position: relative;
    max-width: 1030px;
    margin: auto;
    min-height: 380px;
    padding: 41px 37px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    @include respond(phone) {
      padding: 29px 24px;
      height: auto;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 22px;
    @include respond(phone) {
      display: none;
    }
    &-buttons {
      position: absolute;
      right: 0;
      top: 0;
      display: flex;
      @include respond(phone) {
        display: none;
      }
    }
    &-button {
      width: 69px;
      height: 69px;
      color: #ffffff;
      background-color: #c1c1c1;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.5s;
      box-sizing: border-box;
      margin-right: 10px;
      cursor: pointer;
      &:last-of-type {
        margin-right: 0;
      }
      & .ce-icon {
        width: 40px;
        height: 40px;
      }
      & .ce-icon--car {
        width: 60px;
        height: 55px;
      }
      &.ce-active {
        background-color: #f1f1f1;
