/*Products Start*/
.ce-products {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 28px;
    margin-top: 28px;
  }
  &__item {
    display: block;
    width: 300px;
    border: 1px solid #353535;
    box-sizing: border-box;
    // margin-right: auto;
    background-color: #ffffff;
    overflow: hidden;
    margin: auto;
    margin-bottom: 20px;
    &-image{
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      width: 100%;
      height: 140px;
      margin-top: 15px;
    }
    &-header {
      position: relative;
      // background-image: url("../images/product/product-img.png");
      background-position: center bottom;
      background-repeat: no-repeat;
      background-size: auto;
      height: 230px;
      padding-top: 20px;
      box-sizing: border-box;
      & img {
        max-width: 100%;
        height: auto;
      }
    }
    &-sale {
      position: absolute;
      transform: rotate(-45deg);
      left: -50px;
      top: -50px;
      height: 100px;
      width: 100px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      padding-bottom: 10px;
      box-sizing: border-box;
      background-color: $primaryColor;
      color: #ffffff;
      font-size: 16px;
    }
    &-hit {
      position: absolute;
      right: 0;
      top: 0;
      width: 70px;
      height: 83px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #e6007e;
      color: #ffffff;
      font-size: 16px;
      text-align: center;
      overflow: hidden;
      padding-bottom: 20px;
      box-sizing: border-box;
      &::after {
        content: "";
        display: block;
        width: 20px;
        height: 50px;
        background-color: #ffffff;
        position: absolute;
        transform: rotate(60deg);
        left: 4px;
        bottom: -34px;
        width: 35px;
        height: 50px;
      }
      &::before {
        content: "";
        display: block;
        width: 20px;
        height: 50px;
        background-color: #ffffff;
        position: absolute;
        transform: rotate(-60deg);
        right: 4px;
        bottom: -34px;
        width: 35px;
        height: 50px;
      }
    }
    &-title {
      text-align: center;
      font-size: 22px;
      line-height: 1;
      & b {
        display: block;
        font-family: $fontBold;
        font-size: 26px;
        text-transform: lowercase;
        &::first-letter {
          text-transform: uppercase;
        }
      }
      & span {
        display: inline-block;
        text-transform: lowercase;
        &::first-letter {
          text-transform: uppercase;
        }
      }
    }
    &-icons {
      list-style: none;
      padding-left: 0;
      margin-left: 15px;
      position: absolute;
      left: 0;
      top: 86px;
      & li {
        margin-bottom: 5px;
      }
    }
    &-stars {
      display: flex;
      justify-content: center;
      margin-top: 0;
      margin-bottom: 7px;
    }
    &-tags {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      padding-left: 21px;
      padding-right: 21px;
      height: 27px;
      overflow: hidden;
     
    }
    &-tag {
      margin: 0 2px 5px 2px;
      color: #353535;
      font-size: 18px;
      &--border {
        border: 1px solid #353535;
        padding: 0px 5px;
      }
      &--energy {
        &::before {
          content: url("../images/icons-png/gas-icon.png");
        }
      }
      &--rain {
        &::before {
          content: url("../images/icons-png/rain-icon.png");
        }
      }
      &--sound {
        &::before {
          content: url("../images/icons-png/sound-icon.png");
        }
      }
    }
    &-body {
      display: flex;
      justify-content: space-between;
      padding-left: 10px;
      padding-right: 21px;
      margin-bottom: 10px;
      margin-top: 24px;
    }
    &-code {
      color: #353535;
      font-size: 14px;
      margin-bottom: 16px;
    }
    &-status {
      color: #2db223;
      font-size: 14px;
      &--red {
        color: $primaryColor;
      }
    }
    &-oldprice {
      color: $primaryColor;
      font-size: 16px;
      text-decoration: line-through;
      // text-align: right;
    }
    &-price {
      color: $primaryColor;
      font-size: 30px;
      font-family: $fontBlack;
    }
    &-controls {
      display: flex;
      justify-content: space-between;
      align-items: center;
      color: #1671d5;
      font-size: 16px;
      padding-left: 13px;
      padding-right: 13px;
      margin-bottom: 22px;
      & .ce-button {
        font-size: 16px;
        & .ce-icon {
          width: 15px;
          height: 15px;
          margin-left: 5px;
        }
      }
    }
    &-fastorder {
      cursor: pointer;
    }
    &-more {
      text-align: center;
      font-size: 18px;
      text-decoration: underline;
      color: #1671d5;
      transition: 0.5s;
      cursor: pointer;
      &:hover {
        color: $primaryColor;
      }
    }
  }
}
/*Products End*/

